.button{display:flex;align-items:center;width:fit-content;text-align:center;border-radius:5px;padding:10px 20px;color:inherit;font-weight:700;cursor:pointer;transition:all .3s;text-align:center;font-family:Roboto;border:1px solid rgba(0,0,0,0)}.red{color:#fff;background-color:#c0392b}.red:hover{background-color:#e74c3c}.green{color:#fff;background-color:#2ecc71}.green:hover{background-color:#1abc9c}.theme{color:#fff;transition:all .3s;font-weight:400;font-size:20px;background-color:#01615f;border:1px solid #01615f;text-transform:uppercase;font-weight:500}.theme:hover{background-color:rgba(1,97,95,.8)}.img{filter:brightness(0) invert(1);width:20px;height:20px;margin-top:3px}